import React, { Component, useEffect, useState } from 'react';
import Axios from 'axios';
export default function Product() {
    const [Customers, setCutomers] = useState([]);
    const getCustomer = () => {
        Axios.get("/admin/customers")
            .then((res) => {
                const par = JSON.stringify(res.data);
                console.log(par)
                setCutomers(res.data)
            })
            .catch((err) => {
                console.log(err);
            })
    }
    useEffect(() => {
        getCustomer()
    },[])
    return (
        <div id="layoutSidenav_content">
            <main>
                <div className="col-xl-12 col-md-12">
                    <div className="card card-static-2 mb-30">
                        <div className="card-title-2">
                            <h4>All Customers</h4>
                            <a href="orders.html" className="view-btn hover-btn">View All</a>
                        </div>
                        <div className="card-body-table">
                            <div className="table-responsive">
                                <table className="table ucp-table table-hover">
                                    <thead>
                                        <tr>
                                            <th style={{ width: 130 }}>Customer ID</th>
                                            <th style={{ width: 130 }}>Name</th>
                                            <th style={{ width: 130 }}>Mobile No.</th>
                                            <th style={{ width: 130 }}>Alt Mobile No.</th>
                                            <th style={{ width: 200 }}>Address</th>
                                            <th style={{ width: 130 }}>City</th>
                                            <th style={{ width: 130 }}>Last Login</th>
                                            <th style={{ width: 100 }}>Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {Customers.map((Customer) => (
                                            <tr >
                                                <td>{Customer.User_ID}</td>
                                                <td>{Customer.Name} </td>
                                                <td>{Customer.MobNo1}</td>
                                                <td>{Customer.MobNo2}</td>
                                                <td>{Customer.Address}</td>
                                                <td>{Customer.City}</td>
                                                <td>{Customer.LastLogin}</td>
                                                <td className="action-btns" >
                                                    <a >
                                                        <i className="fas fa-eye " />
                                                    </a>
                                                    {/* <a>
                                                        <i className="fas fa-edit " />
                                                    </a> */}
                                                </td>
                                            </tr>
                                        ))}
                                        
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </main>

        </div>

    );
}

